
.home {
  font-size: 14px;
}
.home p {
    margin-bottom: 10px;
}
.home .view-box {
    padding-top: 30px;
    padding-bottom: 30px;
}
.home .block-title {
    margin-top: 30px;
    margin-bottom: 60px;
    font-size: 26px;
}
.top-nav {
  position: absolute;
  top: 20px;
  left: 0;
  width: 100%;
}
.top-nav ul {
    float: right;
}
.top-nav li {
    display: inline-block;
    margin-left: 30px;
}
.top-nav a {
    color: #fff;
}
.top-nav a.router-link-active, .top-nav a:hover {
      text-decoration: underline;
}
.banner {
  width: 100%;
  padding: 120px 0 80px;
  background-color: #2d8cf0;
  color: #fff;
  font-size: 14px;
}
.banner .wrapper {
    position: relative;
}
.banner h1 {
    margin-bottom: 30px;
}
.banner span {
    margin-right: 20px;
}
.experience .experience-item {
  margin-bottom: 30px;
}
.experience .experience-item hr {
    margin: 10px 0;
}
.experience .experience-item h2 {
    line-height: 2;
}
.experience .experience-item h4 {
    line-height: 2;
}
.experience .experience-item h4 span {
      margin-right: 30px;
}
.experience .experience-item p {
    margin-bottom: 10px;
}
.experience .experience-item .item-projects {
    position: relative;
    padding-left: 70px;
}
.experience .experience-item .item-projects > p {
      position: absolute;
      left: 0;
}
.experience .experience-item dd {
    color: #666;
    font-size: 12px;
}
.contact li {
  margin-bottom: 10px;
}
.introduce dl {
  margin-bottom: 20px;
}
.introduce dl dd {
    margin-bottom: 10px;
}



