.row-box {
    &.row,
    .row {
        [class*="col-xs-"] {
            min-height: 40px;
            padding: 3px;
            line-height: 34px;
        }
        .label {
            width: 80px;
            display: inline-block;
            text-align: right;
        }
    }
}


